.sliderLogin {
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
	height: 70vh;
	margin-top: 12.5px;
}

.hide-container-divtrue {
	display: none;
}

.sliderLogintrue {
	display: none;
}

.sliderLogin h1 {
    font-weight: bold;
    margin: 0;
}

.sliderLogin h2 {
    text-align: center;
}

.sliderLogin p {
    font-size: 14px;
    font-weight: 100;
    line-height: 20px;
    letter-spacing: 0.5px;
    margin: 20px 0 30px;
} 

.sliderLogin button {
	border-radius: 20px;
	border: 1px solid #282c34;
	background-color: #282c34;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}

button:active {
	transform: scale(0.95);
}

button:focus {
	outline: none;
}

.sliderLogin button.ghost {
	background-color: transparent;
	border-color: #FFFFFF;
}

.sliderLogin .Form {
	background-color: #FFFFFF;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 50px;
	height: 100%;
	text-align: center;
}

.sliderLogin input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.container {
	background-color: #fff;
	border-radius: 10px;
  	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
			0 10px 10px rgba(0,0,0,0.22);
	position: relative;
	overflow: hidden;
	width: 768px;
	max-width: 100%;
	min-height: 500px;
}

.containertrue {
	display: none;
}

.form-container {
	position: absolute;
	top: 0;
	height: 100%;
	transition: all 0.6s ease-in-out;
}

.sign-in-container {
	left: 0;
	width: 50%;
	z-index: 2;
}

.container.right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.container.right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	animation: show 0.6s;
}


.overlay-container {
	position: absolute;
	top: 0;
	left: 50%;
	width: 60%;
	height: 108%;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
}

.container.right-panel-active .overlay-container{
	transform: translateX(-100%);
}

.overlay {
	background: linear-gradient(to right, rgb(42, 173, 99), rgb(233, 48, 48));
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
  	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  	transform: translateX(50%);
}

.overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.overlay-panel p {
    width: 70%;
}


.overlay-left {
	transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
	transform: translateX(0);
}

.overlay-right {
	right: 0;
    transform: translateX(0);
}

.container.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.createMessage {
	color: rgb(42, 173, 99);
	font-weight: bold;
}

.createMessagered {
	color: rgb(233, 48, 48);
	font-weight: bold;
}

.loginMessage {
	color: rgb(42, 173, 99);
	font-weight: bold;
}

.loginMessagered {
	color: rgb(233, 48, 48);
	font-weight: bold;
}

.image-crop {
	display: none;
	position: fixed;
    transform: scale(0);
}

.smaller-pictures {
	font-size: 0.8rem;
}